export default {
	data() {
		return {
			
		}
	},
	methods: {
		//点击会员管理
		toVipManage() {
			uni.navigateTo({
				url: '/pages/vipManage/vipManage'
			});
		},
		//点击商品图片跳转到商品详情页
		toGoodsDetailsInfo() {
			uni.navigateTo({
				url: '/pages/goodsDetailsInfo/goodsDetailsInfo'
			});
		},
		// 去年卡会员套餐商品详情页面
		toVipGoodsDetailsInfo() {
			uni.navigateTo({
				url: '/pages/vipGoodsDetailsInfo/vipGoodsDetailsInfo'
			});
		},
		// 去购物车
		goGreenCart() {
			uni.switchTab({
				url: '/pages/greenCart/greenCart'
			});
		},
		// 去查看更多页面
		goDetails() {
			uni.navigateTo({
				url: '/pages/details/details'
			});
		}
	}
